body {
  background-color: #FFFEFD;
  margin: 0;
  color: #1A212F;
}

* {
  margin: 0;
}

.header {
  padding: 15px;
  display: flex;
  justify-content: space-between;
}
.header__nav {
  display: none;
}
.header__logo-svg_ipad {
  display: none;
}

@media screen and (min-width: 768px) {
  .header {
    padding: 12px 45px;
    min-height: 76px;
